import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Platform } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { ConceptModalPage } from '../concept-modal/concept-modal'
import { RootModalPage } from '../root-modal/root-modal'


import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

import * as d3 from 'd3';

export type DataType = {x: any, y: any};

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

	ROOT_NODE_RADIUS = 100;

	margin = {top: 12, right: 12, bottom: 12, left: 12};
	width: number;
  height: number;

  concept: string;

	colors:Array<any>;
	color: any;

	categories:Array<any>;

  svg: any;
  g: any;
	zoom: any;

  simulation: any;
  graph = {
    "nodes": [],
    "links": [],
  };

  node:any;
  link:any;
  text:any;

  cnt:number;
  group: number;

  constructor(public navCtrl: NavController,
              public platform: Platform,
              public modalCtrl: ModalController,
              public http:Http) {

    this.concept = "";
    this.cnt = 1;
    this.group = 1;

		this.colors = [
			{color:'red', hex:'#f44336'},
			{color:'pink', hex:'#e91e63'},
			{color:'purple', hex:'#9c27b0'},
		  {color:'indigo', hex:'#3f51b5'},
			{color:'blue', hex:'#2196f3'},
			{color:'cyan', hex:'#00bcd4'},
			{color:'teal', hex:'#009688'},
			{color:'green', hex:'#4caf50'},
			{color:'yellow', hex:'#ffeb3b'},
			{color:'orange', hex:'#ff9800'},
			{color:'brown', hex:'#795548'},
			{color:'grey', hex:'#9e9e9e'},
			{color:'blue-grey', hex:'#607d8b'},
			{color:'amber', hex:'#ffc107'},
			{color:'lime', hex:'#cddc39'},
			{color:'black', hex:'#000000'}
		];

		this.categories = [];

    this.zoom = d3.zoom()
			.scaleExtent([0.25, 4])
			.on("zoom", this.zoomed.bind(this));

  }

  ionViewDidLoad() {
    this.platform.ready().then((readySource) => {
      this.width = this.platform.width() - this.margin.left - this.margin.right ;
      this.height = this.platform.height() - this.margin.top - this.margin.bottom;
      this.initSvg();
    });
  }

	startWizard ( ){

		var modal = this.modalCtrl
			.create(RootModalPage, { concept: this.concept })

		modal.onDidDismiss(data => {
			this.categories.push({name: 'main concept', color:data.color});
      this.startWeb( data );
    });
		modal.present();

	}

	cloudShape(  ){
		return "M 45 200 A 40 40 0 0 1 40 120 A 30 30 0 1 1 115 105 C 155 90 160 130 160 130 A 30 30 0 1 1 165 200 Z";
	}

	wrap(text, width) {
	  text.each(function() {
	    var text = d3.select(this),
	        words = text.text().split(/\s+/).reverse(),
	        word,
	        line = [],
	        lineNumber = 0,
	        lineHeight = 1.1, // ems
	        y = text.attr("y"),
	        dy = parseFloat(text.attr("dy")),
	        tspan = text.text(null).append("tspan").attr("x", 0).attr("y", y).attr("dy", dy + "em");
	    while (word = words.pop()) {
	      line.push(word);
	      tspan.text(line.join(" "));
	      if ((<SVGTSpanElement>tspan.node()).getComputedTextLength() > width) {
	        line.pop();
	        tspan.text(line.join(" "));
	        line = [word];
	        tspan = text.append("tspan").attr("x", 0).attr("y", y).attr("dy", ++lineNumber * lineHeight + dy + "em").text(word);
	      }
	    }
  	});
	}


  startWeb( data ){
    if(this.concept.trim() === "") return;

    this.svg.selectAll("*").remove();
    this.svg.append('svg:defs')
      .append("svg:pattern")
        .attr("id", "fill-0")
        .attr("width", this.ROOT_NODE_RADIUS * 2)
        .attr("height", this.ROOT_NODE_RADIUS * 2)
        .attr("patternUnits", "userSpaceOnUse")
        .append("svg:image")
				.attr("x", 0)
        .attr("y", this.ROOT_NODE_RADIUS / 2)
        .attr("xlink:href", data.img_url)
        .attr("width", this.ROOT_NODE_RADIUS * 2)
        .attr("height", this.ROOT_NODE_RADIUS * 2)
				.attr("preserveAspectRatio", "xMidYMid slice");
				// build the arrow.


    this.graph.nodes = [];
    this.graph.links = [];
    this.simulation = d3.forceSimulation()
        .force("link", d3.forceLink().id(function(d:any) { return d.id; }).distance(this.ROOT_NODE_RADIUS * 2).strength(1))
        .force("charge", d3.forceManyBody())
        .force("collision", d3.forceCollide(this.ROOT_NODE_RADIUS * 2))
        .force("center", d3.forceCenter((this.width / 2) + this.ROOT_NODE_RADIUS * 2, (this.height / 2) + this.ROOT_NODE_RADIUS * 2));

    this.graph.nodes[0] = {
      "id": 0,
      "label": this.concept,
      "img_url": data.img_url,
      "group": 1,
      "fx": this.width / 2,
      "fy": this.height / 2,
    };

		this.link = this.svg.append("g")
				.attr("class", "links")
			.selectAll("line")
			.data(this.graph.links)
			.enter().append("line")

		this.node = this.svg.append("g")
        .attr("class", "nodes")
      .selectAll("g")
      .data(this.graph.nodes)
      .enter().append("svg:path")
				.attr("d", (d) => { return this.cloudShape();})
				.attr("class", "shape")
        //.attr("r", this.ROOT_NODE_RADIUS)
				//.attr("cx", 200)
				//.attr("cy", 125)
        .style("fill", () => { return data.img_url === null ? this.colors.find(x => x.color === data.color).hex : "url(#fill-0)"})
				.style("stroke-width", 5)
				.style("stroke-linejoin", "round")
				.style("stroke", () => { return this.colors.find(x => x.color === data.color).hex })

        .call(d3.drag()
            .on("start", this.dragstarted.bind(this))
            .on("drag", this.dragged.bind(this))
            .on("end", this.dragended.bind(this)))
        .on('click', this.clicked.bind(this));

    this.text = this.svg.append("g").attr("class", "labels").selectAll("g")
        .data(this.graph.nodes)
      .enter()
			.append("text")
      .attr("y", this.ROOT_NODE_RADIUS + 25)
			.attr("dy", 0)
			.attr("x", 0)
			.style("text-anchor", function(d) { return "middle"; })
			.style("fill", () => { return this.colors.find((x) => x.color === data.color).hex })
      .text(function(d) { return d.label; })
			.call(this.wrap, this.ROOT_NODE_RADIUS*2);


    this.simulation
        .nodes(this.graph.nodes)
        .on("tick", this.ticked.bind(this));

    this.simulation.force("link")
        .links(this.graph.links);
  }

  dragstarted( d ){
    d3.event.sourceEvent.stopPropagation();
    if(d.id === 0) return;
    if (!d3.event.active) this.simulation.alphaTarget(0.3).restart();
    d.fx = d.x;
    d.fy = d.y;
  }

  dragged( d ){
    if(d.id === 0) return;
      d.fx = d3.event.x;
      d.fy = d3.event.y;
  }

  dragended( d ){
    d3.event.sourceEvent.stopPropagation();
    if(d.id === 0) return;

    if (!d3.event.active) this.simulation.alphaTarget(0);
    d.fx = null;
    d.fy = null;
  }

  ticked( ){
		this.link
				.attr("x1", function(d) { return d.source.x; })
				.attr("y1", function(d) { return d.source.y; })
				.attr("x2", function(d) { return d.target.x; })
				.attr("y2", function(d) { return d.target.y; });

		this.node
				.attr("transform", (d) => {
					return "translate(" + (d.x - this.ROOT_NODE_RADIUS) + ","
					+ (d.y - this.ROOT_NODE_RADIUS) + ")";
					});

		this.text
       .attr("transform", function(d) { return "translate(" + (d.x) + "," + (d.y) + ")"; });
  }

  clicked ( d ){
    if (d3.event.defaultPrevented) return;

		var modal = this.modalCtrl
			.create(ConceptModalPage, { parentConcept: this.concept })

		modal.onDidDismiss(data => {
			if(data == null) return;
			if(d.group === 1) this.group++;
			var n = {	id: ""+ this.cnt++,
								group: d.group === 1 ? this.group : d.group,
								label: data.label,
								color: data.color,
								img_url: data.img_url }
			this.graph.nodes.push(n);
			this.graph.links.push({source: d.id, target: n.id, group: n.group, color:n.color});

			this.svg.select("defs")
			.append("svg:pattern")
			.attr("id", "fill-"+n.id)
			.attr("width", this.ROOT_NODE_RADIUS * 2)
			.attr("height", this.ROOT_NODE_RADIUS * 2)
			.attr("patternUnits", "userSpaceOnUse")
			.append("svg:image")
			.attr("x", 0)
			.attr("y", this.ROOT_NODE_RADIUS / 2)
			.attr("xlink:href", n.img_url)
			.attr("width", this.ROOT_NODE_RADIUS * 2)
			.attr("height", this.ROOT_NODE_RADIUS * 2)
			.attr("preserveAspectRatio", "xMidYMid slice");
			/*
			this.svg.select('defs')
				.append('marker')
					.attr('id','arrowhead-'+n.id)
					.attr(  'viewBox','-0 -5 10 10')
					.attr(  'refX',24)
					.attr(  'refY',0)
					.attr(  'orient','auto')
					.attr(  'markerWidth',12)
					.attr(  'markerHeight',12)
					.attr(  'xoverflow','visible')
					.append('svg:path')
					.attr('d', 'M 0,-5 L 10 ,0 L 0,5')
					.attr('fill', this.colors.find(x => x.color === n.color).hex)
					.style('stroke','none');
					*/
			this.restart();
	   });

		modal.present();

  }

 flatten(root) {
  	var nodes = [], i = 0;

	  function recurse(node) {
	    if (node.children) node.children.forEach(recurse);
	    if (!node.id) node.id = ++i;
	    nodes.push(node);
	  }

	  recurse(root);
	  return nodes;
	}


  restart(){
    var self = this;
    this.link = this.link.data(this.graph.links, function(d) {return d.source.id + "-" + d.target.id; });
    this.link.exit().remove();
    this.link = this.link.enter()
			.append("line")
				//.style("stroke", d => {return self.color( d.group )})
				.style("stroke", (d) => { return self.colors.find(x => x.color === d.color).hex })
				.style("stroke-width", 3)
				.attr("class", "link").merge(this.link);
				//.attr('marker-end',d => {return 'url(#arrowhead-'+d.target+')'})

    this.node = this.node.data(this.graph.nodes, function(d) { return d.id;});
    this.node.exit().remove();
    this.node = this.node.enter()
			//.append('circle')
      .append("svg:path")
				.attr("d", (d) => { return this.cloudShape();})
				.attr("class", "shape")
				.attr("dy", -this.ROOT_NODE_RADIUS)
				.attr("x",0)
				//.style("fill", d => {return "url(#fill-" + d.id + ")"})
				//.style("stroke", d => {  return self.color( d.group )})
				.style("stroke-width", 3)
				.style("stroke-linejoin", "round")
				.style("fill", (d) => { return d.img_url === null ? this.colors.find(x => x.color === d.color).hex : "url(#fill-" + d.id + ")"})
				.style("stroke", (d) => { return this.colors.find(x => x.color === d.color).hex }).merge(this.node)


//				.attr("cx", d => {return (d.id === 0) ? self.ROOT_NODE_RADIUS : self.ROOT_NODE_RADIUS / 2})
//				.attr("cy", d => {return (d.id === 0) ? self.ROOT_NODE_RADIUS : self.ROOT_NODE_RADIUS / 2})
//        .attr("r", d => {return (d.id === 0) ? self.ROOT_NODE_RADIUS : self.ROOT_NODE_RADIUS / 2}).merge(this.node)
        .call(d3.drag()
            .on("start", this.dragstarted.bind(this))
            .on("drag", this.dragged.bind(this))
            .on("end", this.dragended.bind(this)))
          .on('click', this.clicked.bind(this));

          //.on('dblclick', this.dblclicked.bind(this));
			this.text = this.text.data(this.graph.nodes, d => {return d.label});

			this.text.exit().remove();
			this.text = this.text.enter()
			.append("text").merge(this.text)
			.attr("y", (d) => { return (d.id === 0) ? this.ROOT_NODE_RADIUS + 25 : self.ROOT_NODE_RADIUS + 25})
			.attr("dy", 0)
			.attr("x", 0)
			.style("text-anchor", "middle")
			//.style("fill", () => { return this.colors.find((x) => x.color === d.color).hex })
			.text( d => d.label)
			.call(this.wrap, this.ROOT_NODE_RADIUS*2);

    // Update and restart the simulation.
    this.simulation.nodes(this.graph.nodes);
    this.simulation.force("link").links(this.graph.links);
    this.simulation.alpha(1).restart();

  }


  initSvg(){
    this.color = d3.scaleOrdinal(d3.schemeCategory20);

    this.svg = d3.select("#concept-web")
			.append("svg")
    	.attr("width",  this.width)
    	.attr("height", this.height);
		this.svg.call(this.zoom);
  }

	onResize( ev ){
		this.width = this.platform.width() - this.margin.left - this.margin.right ;
		this.height = this.platform.height() - this.margin.top - this.margin.bottom;

		this.svg
		.attr("width",  this.width)
		.attr("height", this.height);

		this.restart();

	}

	zoomed( ){
		this.svg.selectAll('g').attr('transform', d3.event.transform);
	}

	zoomIn( ev ){
		this.transition(1.25);
	}

	zoomOut( ev ){
		this.transition(0.75);
	}

	transition(zoomLevel) {
	  this.svg.transition()
	      .delay(50)
	      .duration(400)
	      .call(this.zoom.scaleBy, zoomLevel);
	}

}



